/* navbar css */
@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;600;700;800;900&display=swap');


.navbar {
  /* min-height: 80px; */
  min-height: 11vh;
  background: #fe8235;
  font-family: 'Maven Pro', sans-serif;
  font-weight: 600;
}
  
.navbar-brand {
  font-size: xx-large; 
  font-weight: bolder;
  cursor: pointer;
}

@media screen and (max-width: 991px) { 
  .basket {
    margin: 10px 0; 
  } 

  .navbar-toggler{
    padding: 8px 5px;
    padding-bottom: 3px;
  }
}

@media screen and (max-width: 767px) {
  .navbar-brand {
    font-size: x-large;
    font-weight: 900;
  }
}

@media screen and (max-width: 575px) {
  .navbar {
    min-height: 40px;
  } 
}

.navbar-toggler{
  height: auto;
  /* border-color: #fe8235; */
  transform: rotate(270deg);
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.4);
}


.counting {
  color: white;
  background-color: black;
  font-size: 14px;
  position: relative;
  top: -15px;
  border-radius: 100px;
  padding: 1px 5px;
  margin-right: 10px;
  font-weight: 700;
}

.card-details-img {
  max-height: 25px;
  margin-left: 10px;
}

.btn{
  background: black;
  color: white;
  margin-bottom: 10px;
}

.navbtn {
  border: none;
  outline: 0;
  padding: 5px 10px;
  font-weight: 500;
  background-color: black;
  text-align: center;
  cursor: pointer;
  font-size: 15px;
  font-family: "Maven Pro", sans-serif;
}

.navbtn:hover {
  color: black;
  background-color: white;
  transition: all 0.5s ease-in-out;
}